.toggle{
    margin: 0 3.5%;
}

.toggle div{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    -ms-flex-align: center;
        align-items: center;
}

.toggle div img{
    width: 60px;
    height: auto;
    display: block;
    margin: auto;
    opacity: 0.75;
}

.toggle-on {
    background-color: rgb(233, 113, 0);
}

.toggle-on img{
    -webkit-filter: invert(100%);
            filter: invert(100%);
}

.toggle-off {
    background-color: rgba(0, 0, 0, 0.25);
}

.description {
    font-size: 2pt;
    color: black;
    max-width: 45px;

    font-family: arial;
    font-size: 0%;
}



